import React from 'react';
import { BrowserRouter, Switch, Route } from "react-router-dom";
import Main from './Components/Pages/Main';
const Router = () => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path="/" render={()=>{
                    return <Main />
                }} />
            </Switch>
        </BrowserRouter>
    )
};

export default Router;